#root{
  min-height: 100vh;
  width: 100vw;
  background-image: url('./assets/background.jpeg');
  background-repeat: no-repeat;
  background-size: cover;
}

form{
  width: 100% ;
  display: flex;
  flex-direction: column; 
}

h2{
  margin: auto;
}

button, input{
  margin: auto;
  margin-top: 1rem;
  border-radius: 5rem;
  background-color: white;
  border: none;
}

button{
  width: max-content;
  cursor: pointer;
  padding: 1rem 2rem;
  border: 2px solid black;
  border-radius: .9rem;
}

input{
  width: 50%;
  padding: 1rem;
}

img{
  width: 20%;
  margin-top: 1rem;
}